import PropTypes from "prop-types";
import React from "react";
import Tab from "react-bootstrap/Tab";
import Nav from "react-bootstrap/Nav";

const ProductDescriptionTab = ({ spaceBottomClass, product }) => {
  return (
    <div className={`description-review-area ${spaceBottomClass}`}>
      <div className='container'>
        <div className='description-review-wrapper'>
          <Tab.Container defaultActiveKey='productDescription'>
            <Nav variant='pills' className='description-review-topbar'>
              <Nav.Item>
                <Nav.Link eventKey='additionalInfo'>Ek Blgiler</Nav.Link>
              </Nav.Item>
              <Nav.Item>
                <Nav.Link eventKey='productDescription'>Uyarlar</Nav.Link>
              </Nav.Item>
              <Nav.Item>
                <Nav.Link eventKey='returns'>ade ve Deiim Prosedr</Nav.Link>
              </Nav.Item>
            </Nav>
            <Tab.Content className='description-review-bottom'>
              <Tab.Pane eventKey='additionalInfo'>
                <div className='product-anotherinfo-wrapper'>
                  <ul>
                    <li>
                      <span>Zemin Rengi</span>
                      {product.base_color}
                    </li>
                    <li>
                      <span>ller</span>
                      {product.dimensions}
                    </li>
                    <li>
                      <span>Arlk</span>
                      {product.weight}
                    </li>
                  </ul>
                </div>
              </Tab.Pane>
              <Tab.Pane eventKey='productDescription'>
              <div className='product-anotherinfo-wrapper'>
                <ul>
                  <li className='list-group-item list-group-item-danger'>
                    Su ile direkt temas ettirmeyiniz.
                  </li>
                  <li className='list-group-item list-group-item-danger'>
                    Bulak makinesi kullanmna uygun deildir.
                  </li>
                  <li className='list-group-item list-group-item-danger'>
                    Nemli bez ile silerek temizleyiniz.
                  </li>
                  <li className='list-group-item list-group-item-danger'>
                    Uzun sre gnete brakmaynz.
                  </li>
                </ul>
                </div>
              </Tab.Pane>
              <Tab.Pane eventKey='returns'>
              <div className='product-anotherinfo-wrapper'>
                <ul>
                  <li >
                    <span>Hzl ade</span>
                      ade ve deiim ilemleri iin iletiim bilgilerini kullanarak talepte bulunmanz durumunda kolay iade ekibimiz size yardmc olacaktr.
                  </li>
                  <li >
                    <span>Detayl Bilgi</span>
                    Detayl bilgi edinmek isterseniz <a href='/iade'><strong>ade ve Deiim Prosedrlerini</strong></a> inceleyebilirsiniz.
                  </li>
                  
                </ul>
                </div>
              </Tab.Pane>
            </Tab.Content>
          </Tab.Container>
        </div>
      </div>
    </div>
  );
};

ProductDescriptionTab.propTypes = {
  productFullDesc: PropTypes.string,
  spaceBottomClass: PropTypes.string,
};

export default ProductDescriptionTab;
